@import url(https://fonts.googleapis.com/css?family=Indie+Flower);

*{
	box-sizing: border-box;
	font-family: "Indie Flower", cursive;
}

.app-logo{
	align-items: left;
	height: 80px;
}

.app-navbar {
    background-color: #333333;
    font-size: 20px;
    height: 100px;
}

.navbar-expand-md .navbar-nav .nav-link.navbar-item-link {
    color: #eeeeee;
    font-weight: bold;
    padding: 20px;
    cursor: pointer;
}

.navbar-expand-md .navbar-nav .navbar-item-link:hover {
    background-color: #4b4b4b;
}

.navbar-expand-md .navbar-nav .navbar-item-link.active {
    background-color: #535353;
}

.nav-dropdown-menu.dropdown-menu {
    background-color: #333333;
    border: 1px solid #949494;
}

.nav-dropdown-menu-item {
    background-color: #333333;
    text-align: center;
    font-weight: bold;
    color: #eeeeee;
}

.nav-dropdown-menu-item:hover {
    background-color: #4b4b4b;
    color: #eeeeee;
}

.content-container {
    width: 100%;
    height: 100%;
    margin-top: 20px;
    margin-bottom: 20px; 
}
.home-title {
	text-align: center;
}

.listing-cards {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.home-card {
	background: #EEEEEE;
	margin: 3px;
	padding: 10px;
	height: 500px;
	width: 60%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
}

.card-image {
	height: 100%;
	margin: 3px;
}

.card-text {
	width: 100%;
	height: 100%;
	margin: 3px;
	padding: 10px;
	color: #EEEEEE;
	background: #333333;
	font-size: 25px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
}
.about-cards {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.about-card {
	background: #EEEEEE;
	margin: 3px;
	padding: 10px;
	height: 300px;
	width: 60%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
}

.me-image {
	height: 100%;
	margin: 3px;
}

.contacts-card {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin: 3px;
}

.contact {
	background: #EEEEEE;
	color: #333333;
	padding: 10px;
	height: 50px;
	width: 60%;
	margin: 3px;
	font-size: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	cursor: default;
}

.contact a {
	text-decoration: none;
	color: #333333;
}

.contact a:hover {
	text-decoration: none;
	color: #333333;
	font-weight: bold;
}

.image-contact {
	margin: 0px 6px 6px 0px;
	height: 20px;
}

